import { useEffect, useState } from "react";
import styles from "./App.module.css"
import AuditCard from "./components/AuditCard";
import Web3 from 'web3';
import masterabi from "./web3/masterabi.json"
import erc20abi from './web3/erc20abi.json'

function App() {
  const [verifyopen, setverifyopen] = useState(false)
  var contractinstance;
  const web3 = new Web3("https://polygon-rpc.com/");
  useEffect(() => {

    const contract = new web3.eth.Contract(
      masterabi,
      "0xf3b4d2797E83AFb0Fe26e0300D967B472550d73E"
    );
    contractinstance = contract;
  });
  const [contractInput, setcontractInput] = useState("")
  const [cardObject, setcardObject] = useState({})
  const [viewuri, setviewuri] = useState("")
  const [noauditmessage, setnoauditmessage] = useState(false)
  async function getAuditInfo() {
    try {
      var res = await contractinstance.methods.getTokenByAuditedContract(contractInput).call()
      if (res > 0) {
        setnoauditmessage(false)
        var erccontract = new web3.eth.Contract(erc20abi, contractInput)
        var name = await erccontract.methods.name().call()
        var symbol = await erccontract.methods.symbol().call()
        var highrisks = await contractinstance.methods.getHighRiskVulnerabilities(res).call()
        var chain = await contractinstance.methods.getChainByToken(res).call()
        var cardobj = {
          name: name,
          symbol: symbol,
          highrisks: highrisks,
          address: contractInput.toLowerCase(),
          chain: chain
        }
        setcardObject(cardobj)
        setverifyopen(true)
        var uri = await contractinstance.methods.tokenURI(res).call()
        setviewuri(uri)
      } else {
        setverifyopen(false)
        setnoauditmessage(true)
      }
    } catch (e) {
      setverifyopen(false)
      setnoauditmessage(true)
      console.log(e)
    }
  }
  return (
    <div>
      <div class={styles.maincontainer}>
        <div>
          <div class={styles.logo} />
          <a class={styles.title}>Auditary<br />
            Revolutionizing security<br />in the DeFi space
          </a>
          <a class={styles.subtitle}>Issuing security audits in the form of an NFT<br />Creating a new form of verifing trust</a>
        </div>
        <div class={styles.toplinebreak}></div>
        <div class={styles.auditcardcontainer}>
          <AuditCard info={{
            name: "Example",
            symbol: "EX",
            address: "0x0000000000000000000000000000000000000000",
            highrisks: "0"
          }} extrastyles={styles.topauditcard} />
          <div class={styles.ellipse1} />
        </div>
      </div>
      <div class={styles.sectiontwo}>
        <p class={styles.sectionparagraph}>Verify the validity of a Smart Contract Audit</p>
        <div class={styles.inputcontainer}>
          <input value={contractInput} onChange={(e) => { setcontractInput(e.target.value) }} onKeyDown={(e) => {
            if (e.key === 'Enter') {
              getAuditInfo()
            }
          }
          } placeholder="Contract Address" class={styles.verifyinput}></input>
          <div onClick={() => { getAuditInfo() }} class={styles.searchicon} />
          {verifyopen && <div class={styles.ellipse2} />}
        </div>
        {verifyopen &&
          <>
            <AuditCard info={cardObject} chainid={1} extrastyles={styles.verifyauditcard} />
            <a href={viewuri} class={styles.openauditbutton}>View full Audit</a>
          </>
        }
        {
          noauditmessage &&
          <p class={styles.noauditmessage}>There is no Audit from us to this contract address<br />
            Please report counterfeit audits
          </p>
        }
      </div>
      <div class={styles.sectionthree}>
        <p class={styles.sectionparagraph}>Request a Smart Contract Audit</p>
        <p class={styles.sectionparagraph}>
          Contact us and learn about the benefits of an Audit.<br />
          Doesn’t matter whether you a project owner, or a potential investor
        </p>
        <div class={styles.contactsection}>
          <a href="https://t.me/Auditary_net" target="_blank" class={styles.contactrow}>
            <div class={`${styles.contacticon} ${styles.telegramicon}`} />
            <p>Telegram</p>
          </a>
          <a href="mailto:auditary@protonmail.com" class={styles.contactrow}>
            <div class={`${styles.contacticon} ${styles.emailicon}`} />
            <p>Email</p>
          </a>
        </div>
      </div>
      <div class={styles.footer}>contact@auditary.net • &copy; 2021 auditary.net</div>
    </div>
  );
}

export default App;
