import React from 'react'
import styles from './AuditCard.module.css'

const AuditCard = ({extrastyles, info}) => {
    return (
        <div class={`${styles.body} ${extrastyles}`}>
            <div class={styles.lock} />
            <div class={`${styles.chainlogo} ${info.chain == 1 ? styles.bsclogo : 0} ${info.chain == 2 ? styles.polygonlogo : 0}`} />
            <a class={styles.title}>AUDITARY</a>
            <a class={styles.subtitle}>SMART CONTRACT AUDIT</a>
            <a class={`${styles.subtitle} ${styles.extramargin}`}>NAME: {info.name}</a>
            <a class={styles.subtitle}>SYMBOL: {info.symbol}</a>
            <a class={styles.subtitle}>{info.address.slice(0,22)}...</a>
            <a class={styles.bottomtext}>{info.highrisks} HIGH RISK VULNERABILITIES</a>
        </div>
    )
}

export default AuditCard
